import mixins from "../../utils/mixins";
import {common} from "../../utils/api/url";
import LayoutLr from "../../components/Layout/LayoutLr";
import MyElTree from "../../components/tree/MyElTree";
import MyPagination from "../../components/MyPagination";
import MyInputButton from "../../components/search/MyInputButton";
import MySex from "../../components/MySex";
import DialogNormal from "../../components/utils/dialog/DialogNormal";
import MySelect from "../../components/MySelect";
import MyCascader from "../../components/utils/select/MyCascader";
import MyDatePicker from "../../components/MyDatePicker";
import MyNormalDialog from "../../components/utils/dialog/MyNormalDialog";
import DrawerRight from "../../components/utils/dialog/DrawerRight";
import {
  MessageError,
  MessageSuccess, MessageWarning,
  nationInfo,
  studentTeachStatus, studyType,
  teacherTypeInfo,
  workEnjoy,
  workTitle
} from "../../utils/utils";
import studentValidater from "../../utils/validater/studentValidater";
export default {
  mixins: [mixins, studentValidater],
  components: {LayoutLr,MyElTree,MyPagination,MyInputButton,MySex,DialogNormal,MySelect,MyCascader,MyDatePicker,MyNormalDialog,DrawerRight},
  data(){
    return {
      tableData: [],
      modalVisible: false,
      dialogLoading: false,
      visibleConfim: false,
      drawerVisible: false,
      drawerLoading: false,
      searchCollege: '',
      searchMajor: '',
      searchGrade: '',
      searchClass: '',
      searchKey: '',
      searchDept: '',
      subTitle: '',
      uploadProcess: '',
      uploadResult: [],
      uploadFile: common.teacher_file + "?fileName=" + encodeURIComponent(this.$t("学生导入模板20180130_v2.xls")),
      uploadAction: common.student_upload,
      loopTimer: null,
      resultList: [],
      form: {
        id: '',
        name: '',
        sex: '',
        nation: '',
        borthday: '',
        idCard: '',
        phone: '',
        email: '',
        innerTime: '',
        classData: [],
        addAccount: '',
        type: '',
        status: '',
        studnetId: '',
        className: '',
        classId: '',
        collegeId: '',
        majorId: '',
        claszDate: '',
        nativePlace: ''
      }
    }
  },
  created() {
    this.init();
  },
  methods: {
    init(){
      let params = {
        page: this.page,
        num: this.num,
        collegeId: this.searchCollege,
        majorId: this.searchMajor,
        grade: this.searchGrade,
        clasz: this.searchClass,
        deleted: 0
      };
      params[this.searchKey['select']] = this.searchKey['input'];
      params = this.$qs.stringify(params);
      this.$axios.post(common.student_list, params).then(res => {
        if (res.data.data){
          console.log(res.data.data);
          this.tableData = res.data.data.list;
          this.total = res.data.data.totalCount;
          this.num = res.data.data.num;
          this.page = res.data.data.currentPage;
        }
      });
    },
    addInfo(){
      this.modalVisible = true;
    },
    editInfo(row){
      this.form = {
        id: row.user_id,
        name: row.real_name,
        studentId: row.student_id,
        sex: ''+row.sex,
        nation: row.nation,
        borthday: row.birthday ? this.$moment(row.birthday).format("YYYY-MM-DD") : '',
        idCard: row.certificate_num,
        phone: row.phone,
        email: row.email,
        innerTime:  row.school_in_time ? this.$moment(row.school_in_time).format("YYYY-MM-DD") : '',
        type: row.attend_type,
        status: row.status,
        addAccount: '',
        className: row.class_name,
        classId: row.class_id,
        collegeId: row.college_id,
        majorId: row.major_id,
        claszDate: this.$moment(row.clasz_date).format("YYYY-MM-DD"),
        nativePlace: row.native_place
      };
      this.$set(this.form, 'classData', [row.college_id, row.major_id, row.grade, row.class_id]);
      this.modalVisible = true;
    },
    deleteInfo(row){
      this.form.id = row.user_id;
      this.subTitle = row.real_name;
      this.visibleConfim = true;
    },
    nodeClick(data){
      this.searchCollege = "";
      this.searchMajor = "";
      this.searchGrade = "";
      this.searchClass = "";
      if (data.unit == 1){
        this.searchCollege = data.id;
      }else if (data.unit == 2){
        this.searchCollege = data.college_id;
        this.searchMajor = data.id;
      }else if (data.unit == 3){
        this.searchMajor = data.major_id;
        this.searchGrade = data.grade;
      }else if (data.unit == 4){
        this.searchClass = data.id;
      }
      this.page = 1;
      this.init();
    },
    search(data){
      this.searchKey = data;
      this.page = 1;
      this.init();
    },
    sizeChange(event){
      this.page = 1;
      this.num = event;
      this.init();
    },
    currentPage(event){
      this.page = event;
      this.init();
    },
    jumpPage(data){
      this.page = data;
      this.init();
    },
    workTitleInfo(val, type){
      return workTitle(type, val);
    },
    studentTeachStatusInfo(val, type){
      return studentTeachStatus(type, val);
    },
    okDialog(){
      let url = "";
      this.$refs['form'].validate((valid) => {
        if (valid) {

          if (this.$refs.selectorDept.$refs.cascaderSelector.getCheckedNodes()[0] == null){
            MessageWarning("请填写班级信息！");
            return;
          }
          this.dialogLoading = true;
          let classData = this.$refs.selectorDept.$refs.cascaderSelector.getCheckedNodes()[0].pathNodes;
          let className = this.$refs.selectorDept.$refs.cascaderSelector.getCheckedNodes()[0].pathLabels;
          /*let deptArr = [];
          for (let i = 0; i < deptData.length; i++){
            deptArr.push(deptData[i].value);
          }*/
          let params = {
            realName: this.form.name,
            studentId: this.form.studentId,
            sex: this.form.sex,
            nation: this.form.nation,
            birthday: this.form.borthday,
            certificateNum: this.form.idCard,
            phone: this.form.phone,
            schoolInTime: this.form.innerTime,
            stuAttendType: this.form.type,
            workEnjoy: this.form.workEnjoy,
            title: this.form.workTitle,
            type: this.form.type,
            addAccount: 'on',
            studentCurrentStatus: this.form.status,
            clasz: classData[3].value,
            collegeId: classData[0].value,
            majorId: classData[1].value,
            attendType: this.form.type,
            status: this.form.status,
            className: className[className.length - 1],
            claszDate: this.form.claszDate,
            nativePlace: this.form.nativePlace
          };
          if (this.form.email){
            params['email']  = this.form.email;
          }
          if (this.form.id != ""){
            url = common.student_edit;
            params['userId'] = this.form.id;
          }else {
            url = common.student_add;
          }
          params = this.$qs.stringify(params);
          this.$axios.post(url, params, {loading: false}).then(res => {
            if (res.data.code == 200){
              this.modalVisible = false;
              this.init();
              MessageSuccess(res.data.desc);
            }else {
              MessageError(res.data.desc);
            }
            this.dialogLoading = false;
          });
        }
      });
    },
    closeDialog(event){
      this.form = {
        id: '',
        name: '',
        sex: '',
        nation: '',
        borthday: '',
        idCard: '',
        phone: '',
        email: '',
        innerTime: '',
        classData: [],
        addAccount: '',
        type: '',
        status: '',
        studnetId: '',
        className: '',
        classId: '',
        collegeId: '',
        majorId: '',
        claszDate: '',
        nativePlace: ''
      };
      this.subTitle = "";
      this.$set(this.form,'deptdata', []);
      this.resetCasadeSelector('selectorDept');
      if (this.$refs['form']){
        this.$refs['form'].resetFields();
      }
    },
    cancelDialog(){
      this.modalVisible = false;
    },
    handleChangeTime(data, type){
      switch (type) {
        case 1:
          this.form.borthday = data;
          break;
        case 2:
          this.form.innerTime = data;
          break;
        case 3:
          this.form.workTime = data;
          break;
      }
    },
    handleChangeSelect(data, type){
      switch (type) {
        case 1:
          this.form.sex = data;
          break;
        case 2:
          this.form.nation = data;
          break;
        case 3:
          this.form.type = data;
          break;
        case 4:
          this.form.status = data;
          break;
      }
    },
    nationInfoText(){
      let arr = nationInfo();
      for (let i = 0; i < arr.length; i++){
        arr[i]['label'] = arr[i].name;
        arr[i]['value'] = arr[i].code;
      }
      return arr;
    },
    studentTeachStatusOption(){
      let obj = studentTeachStatus('get');
      let arr = [];
      for (let i in obj){
        arr.push({
          label: obj[i],
          value: i
        });
      }
      return arr;
    },
    studyTypeOption(){
      let obj = studyType('get');
      let arr = [];
      for (let i in obj){
        arr.push({
          label: obj[i].name,
          value: obj[i].code
        });
      }
      return arr;
    },
    workTitleOption(){
      let obj = workTitle('get');
      let arr = [];
      for (let i in obj){
        arr.push({
          label: obj[i],
          value: i
        });
      }
      return arr;
    },
    teacherTypeText(){
      let obj = teacherTypeInfo();
      let arr = [];
      for (let i = 0; i < obj.length; i++){
        arr.push({
          label: obj[i].name,
          value: obj[i].code
        });
      }
      return arr;
    },
    handleCascaderChange(data){
      this.form.classData = data;
      if (data.length == 4){
        let createTime = this.$refs.selectorDept.$refs.cascaderSelector.getCheckedNodes()[0].pathNodes[3].data.create_time;
        this.form.claszDate = this.$moment(createTime).format("YYYY-MM-DD");
      }
    },
    handleOkChange(data) {
      this.dialogLoading = true;
      let url = "";
      let params = {
        userId: this.form.id
      }
      url = common.student_delete;
      params = this.$qs.stringify(params);
      this.$axios.post(url, params).then(res => {
        if (res.data.code == 200){
          this.init();
          MessageSuccess(res.data.desc);
        }else {
          MessageError(res.data.desc);
        }
        this.visibleConfim = false;
        this.dialogLoading = false;
      });
    },
    handleCancelChange(data) {
      this.visibleConfim = false;
    },
    closeDrawerDialog(event){
      this.uploadProcess = '';
      this.uploadResult = [];
      clearTimeout(this.loopTimer);
      this.loopTimer = null;
      this.drawerVisible = event;
    },
    closeDrawDialog(event){
      this.drawerVisible = false;
    },
    cancelDrawDialog(){
      this.uploadProcess = '';
      this.uploadResult = [];
      this.drawerVisible = false;
    },
    uploadInfo(){
      this.drawerVisible = true;
    },
    uploadSuccess(res, file){
      if (res.code == 200){
        this.uploadProcess = res.desc;
        this.loopUploadResult(res.data);
      }else {
        this.uploadProcess = this.$t("文件上传成功,正在导入文件...");
        this.resultList = [];
        if (res.data){
          for (let i in res.data){
            this.uploadResult.push(res.data[i]);
          }
        }else {
          this.uploadResult = [res.desc];
        }
      }
    },
    uploadError(res, file){
      this.uploadProcess = res.data.data;
    },
    loopUploadResult(uuid){
      this.getUploadResult(uuid);
    },
    getUploadResult(uuid){
      let _self = this;
      let num = 0;
      clearTimeout(this.loopTimer);
      let params = {
        uuid: uuid,
        action: 2
      };
      this.$axios.get(common.upload_loop_result, {params: params}).then(res => {
        let result = "";
        if (res.data.code == 200){
          let arrResult = [];
          if (res.data.data){
            for (let i = 0; i < res.data.data.length; i++){
              //设置结果列表
              if (res.data.data[i].line){
                arrResult.push(this.$t("第") + res.data.data[i].line + this.$t("行") + JSON.parse(res.data.data[i].mess).join());
              }else {
                arrResult.push(JSON.parse(res.data.data[i].mess).join());
              }

              if (res.data.data[i].status == 1){
                num++;
              }
              // if (res.data.data[i].status == 1){
              //   //this.loopStatus = true;
              //   this.uploadResult = arrResult;
              //   clearTimeout(this.loopTimer);
              //   break;
              // }else {
              //   this.loopTimer = setTimeout(function () {
              //     _self.getUploadResult(uuid)
              //   }, 10000);
              // }
            }
            if (num > 0){
              this.uploadResult = arrResult;
              clearTimeout(this.loopTimer);
              this.loopTimer = null;
              this.uploadProcess = this.$t("导入操作已完成，请查看上传结果！");
            }else {
              this.loopTimer = setTimeout(function () {
                _self.getUploadResult(uuid)
              }, 10000);
            }
          }else {
            this.uploadResult = [this.$t("上传停止！")];
            clearTimeout(this.loopTimer);
          }
        }
      });
    }
  }
}
